<script>
import dataList from '../assets/Data.json'
import { ref } from 'vue';
import {Swiper, SwiperSlide} from 'swiper/vue'
import { Pagination, Navigation, Autoplay, Thumbs, FreeMode} from 'swiper'
import "swiper/css/thumbs"
import "swiper/css/free-mode"
import 'swiper/css/bundle';
import 'swiper/css/navigation';
export default {
  name: "MainView",
  data() {
    return {
      WorkText: dataList.WorkText,
      tool: dataList.ProjectList.tool,
      Skill: dataList.Skill,
      Profile: dataList.Profile,
      WorkImg: dataList.WorkImg,
      Giturl: "https://github.com/Zamteng9?tab=repositories",
      Project: dataList.Project,
      SideTitle: dataList.SideTitle,
      SideTxt: dataList.SideTxt,
      ProjectList: dataList.ProjectList,
      dataList: [],
      Modules: [Pagination, Navigation, Autoplay, FreeMode, Thumbs],
      ArrayList: null
    }
  },
  props: {
    section: String
  },
  components:{
    Swiper,
    SwiperSlide
  },
  
  setup() {
    const thumbsSwiper = ref(null);
    const setThumbsSwiper = (swiper) => {
      thumbsSwiper.value = swiper;
    };
    return {
      Thumbs,
      thumbsSwiper,
      setThumbsSwiper,
      FreeMode,Navigation,
      modules: [FreeMode,Navigation,Thumbs],
    };
  },


  methods:{
    toTop:function(){
      const rect = this.$refs.Portfolio.getBoundingClientRect();
      // console.log(rect);
      const offset = rect.top + window.scrollY;
      window.scrollTo({
        top: offset,
        behavior: 'smooth'
      })
    },
  },
  mounted() {
    this.ArrayList = this.$refs
  },
  watch: {
    section() {
      const rect = this.ArrayList[this.section].getBoundingClientRect();
      const offset = rect.top + window.scrollY - 60;
      window.scrollTo({
        top: offset,
        behavior: 'smooth'
      })
    }
  }
}

</script>

