</template>

<script>
// import Home from './components/HomeView.vue'
import Nav from './components/NavView.vue'
import TextTop from './components/TextView.vue'
import Main from './components/MainView.vue'
import Footer from './components/FooterView.vue'
export default {
  name: 'App',
  components: {
    // Home,
    Nav,
    TextTop,
    Main,
    Footer
  },
  data() {
    return {
      section: '',
    }
  },
  methods: {
    moveSection(selectSec){
      this.section = selectSec
    }
  },
}
</script>

